.single{
    display: flex;
    width: 100%;
    .singleContainer{
       flex: 6; 

        .top{
            padding: 20px;
            display: flex;
            gap: 20px;
            // align-items: center;

            .left{
                
             flex: 1;
             box-shadow: 13px 12px 9px -1px rgba(207,184,232,0.53);
              -webkit-box-shadow: 13px 12px 9px -1px rgba(207,184,232,0.53);
             -moz-box-shadow: 13px 12px 9px -1px rgba(207,184,232,0.53);
             padding: 20px;
             position: relative;

                .editButton{
                    position: absolute;
                    top: 0;
                    right: 0;
                    padding: 5px;
                    font-size: 12px;
                    color: purple;
                    background-color: rgba(30, 23, 95, 0.174);
                    cursor: pointer;
                    border-radius: 0px 0px 0 5px;
                }

                .item{
                    display: flex;
                    gap: 20px;

                    .itemImg{
                        width: 100px;
                        height: 100px;
                        border-radius: 50%;
                        object-fit: cover;

                    }

                    .details{
                        .itemTitle{
                            margin-bottom: 10px;
                            color: #555;
                        }
                        .detailItem{
                            margin-bottom: 10px;
                            font-size: 14px;

                            .itemKey{
                                font-weight: bold;
                                color: gray;
                                margin-right: 5px;
                            }
                            .itemValue{
                                font-weight: 300;
                            }
                        }
                    }
                }
            }

            .right{
                 flex: 2;
            }
        }
        .title{
            font-size: 16px;
            color: lightgray;
            margin-bottom: 20px;
        }
    }
}